html, body {
    overscroll-behavior: none;
    height: 100%;
    /* overflow-x: hidden; */
}

.option:hover {
    border-bottom: 1px solid black;
    cursor: pointer;
}

.mailingList {
    transition: .5s;
    cursor: pointer;
}

.mailingList:hover {
    opacity: 50%;
}

.instaLogo {
    transition: .5s;
}

.instaLogo:hover {
    opacity: 50%;
}

input {
    width: 40%;
    height: 40px;
    border-radius: 10px;
}
